import React, { Fragment } from 'react';
import {

    Container,
    makeStyles,
    Grid,
    Typography,
    Box,
    useTheme,
    useMediaQuery,
    Link
}
from '@material-ui/core'

import Theme from '../ThemeBranding';
import CricleArrow from '../../assets/svg/creators/arrowCircle.svg';
import rateTick from '../../assets/svg/creators/rateTick.svg';


const usestyles = makeStyles({
    creatorsname: {
        display: '-webkit-box',
    display: '-ms-flexbox',
    display: 'flex',
    WebkitBoxAlign: 'start',
    MsFlexAlign: 'start',
    alignItems: 'flex-start',
        '@media (max-width: 1280px)' :{
            marginBottom: 40   
        },
        '@media (max-width: 600px)' :{
            display: 'block'  
        },
    },
    creatoravatar:{
        borderRadius: '50%',
        marginRight: 35,
        '@media (max-width: 600px)' :{
            marginRight: 0,
            marginBottom: 35,
        },
        '& img':{
            objectFit: 'contain',
        }

    } , 
    creatorbody:{
        '& h3': {
            color: '#fff',
            fontSize: '24px',
            fontFamily: Theme.Montserrat,
            fontWeight: 500,
            marginBottom: 15,
            marginTop: 0
        },
        '& p': {
            color: '#dfdfe6',
            fontSize: '16px',
            fontFamily: Theme.Montserrat,
            fontWeight: 400,
            maxWidth: '436px',
            '@media (max-width: 1340px)':{
                maxWidth: '360px',
            },
            '@media (max-width: 1280px)':{
                maxWidth: '436px',
            },
        }
    },
    creatorimg:{
        '& img':{
            objectFit: 'contain',
            '@media (max-width: 600px)' :{
               maxWidth: '100%'
            },
        }
    },
    flexbox:{
     display: Theme.flexbox,
     '@media (max-width: 960px)': {
        display: 'block',
        textAlign: 'center'
    }
    },
    customspacingContainer: {
        paddingLeft: 150,
        zIndex: 9,
        position: 'relative',
        '@media (max-width: 1450px)':{
            paddingLeft: 100,
        },
        '@media (max-width: 1200px)':{
            paddingLeft: 20,
            paddingRight: 20,

        },
       
    },
    creatorbg:{
       padding: '60px 0',
       position:'relative',
     
       '&::after':{
           content: '""',
            position: 'absolute',
            left: 0,
            top: 0,
            right: 0,
            bottom: 0,
            width: '100%',
            height: '100%',
            WebkitBackdropFilter: 'blur(30px)', 
             backdropFilter: 'blur(30px)',
             background: '#09090966'
       },
     
        

    },
    criclearrowlink: {
        marginTop: 15,
        display: 'inline-block'
    },
    ratetick:{
        marginLeft: 10
    },
    
});
export default function Creator(props){
    const classes = usestyles();
    const theme = useTheme();
    const isMobile = useMediaQuery(theme.breakpoints.down('lg'), {
        defaultMatches: true
      });
    const isMobileSmall = useMediaQuery(theme.breakpoints.down('sm'), {
        defaultMatches: true
      });
    return(
        <Fragment>
                <div 
                className={classes.creatorbg} 
                style={{background: 'url('+ props.sideimgfirst +') no-repeat center', backgroundSize: '100% 100%'}}>
                    <Container disableGutters maxWidth="false" className={classes.customspacingContainer}>
                         <Grid container alignItems="center" justify={`${isMobile}` ? 'center' : 'left'}>
                             <Grid item lg={6} >
                                <Box component="div" className={classes.creatorsname}>
                                    <Box className={classes.creatoravatar} component="div">
                                        <img src={props.thumbnail}/>
                                    </Box>
                                    <Box className={classes.creatorbody} component="div">
                                        <Box component="h3">
                                            {props.title} 
                                            <img src={rateTick} className={classes.ratetick}/>
                                        </Box>
                                            <Typography variant="body2">
                                             {props.description}
                                            </Typography>
                                            <Link href="#" className={classes.criclearrowlink}> 
                                                <img src={CricleArrow}/>
                                            </Link>
                                
                                    </Box>
                                </Box>
                             </Grid>    
                             <Grid item lg={6} justify="flex-end" className={classes.flexbox} >
                                    <Box className={classes.flexbox}>
                                        <Box component="div" mx={isMobileSmall ? 0 : 2} className={classes.creatorimg} mb={isMobile ? 3 : 0} >
                                            <img src={props.sideimgfirst}/>
                                        </Box>
                                        

                                         <Box component="div" className={classes.creatorimg}>
                                            {props.sideimgsecond ? 
                                              <img src={props.sideimgsecond}/> 
                                              :
                                              null
                                        }
                                       
                                         </Box> 
                                    </Box>
                             </Grid>
                         </Grid>
                    </Container>
                </div>
        </Fragment>
    );

}