import React, { Fragment } from 'react';
import {
    makeStyles,
    Container,
    Grid,
    Switch,
    FormGroup,
    FormControlLabel,
    Button,
    Popper,
    Grow,
    Paper,
    MenuList,
    MenuItem,
    ClickAwayListener,
    useTheme,
    useMediaQuery,
    withStyles,
    Typography
}
from '@material-ui/core';
import Theme from './ThemeBranding';


const useStyles = makeStyles({
    filterbg:{
        background: Theme.Dark_Gray,
        paddingBottom: 30,
        marginTop: -2
    },
    themecolor:{
        color: Theme.BlueColor
    },
    dropdownright: {
        display: 'flex',
        '@media (max-width: 960px)':{
            marginTop: 30
        }
    },
    togglebtndropdown: {
        color: '#fff',
        fontSize: '16px',
        fontWeight: '400',
        textTransform: 'capitalize',
        fontFamily: Theme.Montserrat,
        background: '#242526',
        borderRadius: '8px',
        padding: '5px 17px',
    },
    filtermaincustom: {
        fontFamily: Theme.Montserrat,
        '@media (max-width: 600px)':{

        }
    },
    // filtergroupm: {
    //     '@media (max-width: 600px)':{
    //        display: 'block',     
    //     },
    //    '& label':{
    //         '@media (max-width: 600px)':{
    //             width: '100%',
    //             justifyContent: 'flex-end'    
    //          },
    //     }

    // },
    dropmenucustom: {
       zIndex: 9999,
       minWidth: '232px',
        '& > div':{
            background: '#242526',
        },
        '& li':{
            color: '#fff',
            fontSize: '14px',
            fontWeight: '400',
            textTransform: 'capitalize',
            fontFamily: Theme.Montserrat,
        }
    },
    filtermc: {
        margin: '0 10px',
        '@media (max-width: 600px)':{
            width: '100%',
            margin: '0 20px',
        }
    },
    filterlabel:{
        fontFamily: Theme.Montserrat,
        fontSize: '16px',
        fontWeight: '400',
        color: '#949499'
    },
    filterlabelwhite:{
        fontFamily: Theme.Montserrat,
        fontSize: '16px',
        fontWeight: '400',
        color: '#fafafa'
    }
});


const AntSwitch = withStyles((theme) => ({
    root: {
      width: 32,
      height: 16,
      padding: 0,
      display: 'flex',
    },
    switchBase: {
      padding: 2,
      color: theme.palette.grey[500],
      '&$checked': {
        transform: 'translateX(17px)',
        color: theme.palette.common.white,
        '& + $track': {
          opacity: 1,
          backgroundColor: Theme.BlueColor,
          borderColor: Theme.BlueColor,
        },
      },
    },
    thumb: {
      width: 12,
      height: 12,
      boxShadow: 'none',
    },
    track: {
      borderRadius: 16 / 2,
      opacity: 1,
      backgroundColor: '#575759',
    },
    checked: {},
  }))(Switch);




export default function FiltersSwitch(){
    const [state, setState] = React.useState({
        verifyonly: false,
        filterone: false,
        filtertwo: false,
        filterthree: false,
      });
    
 
      const handleChange = (event) => {
        setState({ ...state, [event.target.name]: event.target.checked });
      };
      
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const anchorRef = React.useRef(null);
    const handleToggle = () => {
        setOpen((prevOpen) => !prevOpen);
      };
      const handleClose = (event) => {
        if (anchorRef.current && anchorRef.current.contains(event.target)) {
          return;
        }
    
        setOpen(false);
      };
      function handleListKeyDown(event) {
        if (event.key === 'Tab') {
          event.preventDefault();
          setOpen(false);
        }
      }
      const prevOpen = React.useRef(open);
      React.useEffect(() => {
        if (prevOpen.current === true && open === false) {
          anchorRef.current.focus();
        }
    
        prevOpen.current = open;
      }, [open]);

      const theme = useTheme();
      const isMobile = useMediaQuery(theme.breakpoints.down('lg'), {
          defaultMatches: true
        });


    

    return(
        <Fragment>
            <div className={classes.filterbg}>
                <Container maxWidth="lg">
                    <Grid container justify={`${isMobile}` ? 'center' : null} alignItems="center">
                        <Grid item md={8}>
                            <FormGroup row className={classes.filtergroupm}>
                                {/* <FormControlLabel
                                    control={
                                    <Switch 
                                    checked={state.verifyonly} 
                                    onChange={handleChange} 
                                    color= 'primary'
                                    name="verifyonly" />}
                                    label="Verified Only"
                                    labelPlacement="start"
                                    style={{color:'#fff'}}
                                    className={classes.filtermaincustom}
                                />
                                <FormControlLabel
                                    control={
                                    <Switch
                                        checked={state.filterone}
                                        onChange={handleChange}
                                        name="filterone"
                                        color= 'primary'
                                    />
                                    }
                                    label="Filter 1"
                                    labelPlacement="start"
                                    style={{color:'#fff'}}
                                    className={classes.filtermaincustom}

                                />
                                <FormControlLabel
                                    control={
                                    <Switch
                                        checked={state.filtertwo}
                                        onChange={handleChange}
                                        name="filtertwo"
                                        color= 'primary'
                                    />
                                    }
                                    label="Filter 1"
                                    labelPlacement="start"
                                    style={{color:'#fff'}}
                                    className={classes.filtermaincustom}

                                />
                                <FormControlLabel
                                    control={
                                    <Switch 
                                    checked={state.filterthree} 
                                    onChange={handleChange} 
                                    color= 'primary'
                                    name="filterthree" />}
                                    label="Filter 1"
                                    labelPlacement="start"
                                    style={{color:'#fff'}}
                                    className={classes.filtermaincustom}
                                /> */}
                                  <FormGroup row className={classes.filtergroupm}>
                                    <Typography component="div" className={classes.filtermc}>
                                        <Grid component="label" container alignItems="center" spacing={1}>
                                        <Grid item  className={state.verifyonly === true ? classes.filterlabelwhite : classes.filterlabel}>Verified only</Grid>
                                        <Grid item>
                                            <AntSwitch checked={state.verifyonly} onChange={handleChange} name="verifyonly" />
                                        </Grid>
                                        </Grid>
                                    </Typography>
                                    <Typography component="div" className={classes.filtermc}>
                                        <Grid component="label" container alignItems="center" spacing={1}>
                                        <Grid item  className={state.filterone === true ? classes.filterlabelwhite : classes.filterlabel}>Filter one</Grid>
                                        <Grid item>
                                            <AntSwitch checked={state.filterone} onChange={handleChange} name="filterone" />
                                        </Grid>
                                        </Grid>
                                    </Typography>
                                    <Typography component="div" className={classes.filtermc}>
                                        <Grid component="label" container alignItems="center" spacing={1}>
                                        <Grid item  className={state.filtertwo === true ? classes.filterlabelwhite : classes.filterlabel}>Filter two</Grid>
                                        <Grid item>
                                            <AntSwitch checked={state.filtertwo} onChange={handleChange} name="filtertwo" />
                                        </Grid>
                                        </Grid>
                                    </Typography>
                                    <Typography component="div" className={classes.filtermc}>
                                        <Grid component="label" container alignItems="center" spacing={1}>
                                        <Grid item  className={state.filterthree === true ? classes.filterlabelwhite : classes.filterlabel}>Filter three</Grid>
                                        <Grid item>
                                            <AntSwitch checked={state.filterthree} onChange={handleChange} name="filterthree" />
                                        </Grid>
                                        </Grid>
                                    </Typography>
                                </FormGroup>
                            </FormGroup>
                        </Grid>
                        <Grid item md={4} justify="flex-end" direction="row" className={classes.dropdownright}>
                            <FormGroup row>
                                 <Typography component="div" className={classes.filtermc}>
                                        <Grid component="label" container alignItems="center" spacing={1}>
                                            <Grid item  className={classes.filterlabel} style={{color:'#949499'}}>Sort By:</Grid>
                                            <Grid item>
                                                <Button
                                                    ref={anchorRef}
                                                    aria-controls={open ? 'menu-list-grow' : undefined}
                                                    aria-haspopup="true"
                                                    onClick={handleToggle}
                                                    className={classes.togglebtndropdown}
                                                >
                                                Auction: Ending Sonest 
                                                </Button>
                                                <Popper open={open} className={classes.dropmenucustom} anchorEl={anchorRef.current} role={undefined} transition disablePortal>
                                                {({ TransitionProps, placement }) => (
                                                    <Grow
                                                    {...TransitionProps}
                                                    style={{ transformOrigin: placement === 'bottom' ? 'center top' : 'center bottom' }}
                                                    >
                                                    <Paper>
                                                        <ClickAwayListener onClickAway={handleClose}>
                                                        <MenuList autoFocusItem={open} id="menu-list-grow" onKeyDown={handleListKeyDown}>
                                                            <MenuItem onClick={handleClose}>ProfileProfile ProfileProfile</MenuItem>
                                                            <MenuItem onClick={handleClose}>My account</MenuItem>
                                                            <MenuItem onClick={handleClose}>Logout</MenuItem>
                                                        </MenuList>
                                                        </ClickAwayListener>
                                                    </Paper>
                                                    </Grow>
                                                )}
                                                </Popper>
                                            </Grid>
                                        </Grid>
                                </Typography>

                        </FormGroup>
                        </Grid>
                    </Grid>
                </Container>
            </div>
        </Fragment>

    );

}